.App {
  text-align: center;
}

body {
  background: -webkit-gradient(linear, left top, right top, from(black), color-stop(gray), to(black));
  background: -webkit-linear-gradient(left, black, gray, black);
  background: -o-linear-gradient(left, black, gray, black);
  background: linear-gradient(to right, black, gray, black);
  color: white;
}
.container {
  display: -ms-flexbox;
  display: flex;
  height: 100vh;
}

.form-container {
  -ms-flex: 1 0 100vw;
      flex: 1 0 100vw;
  text-align: center;
}
 label, select, button, input {
   font-size: 1em;
   margin: 10px;
 }

.images-container {
  -ms-flex: 1 0 100vw;
      flex: 1 0 100vw;
}

img {
  width: 50vw;
  height: auto;
  border-width: 5px 5px 20px 5px;
  border-color: white;
}

.img-caption {
  margin: 0 0 10px 0;
}

.loading-img {
  -webkit-animation: loading-img-spin infinite 1s linear;
          animation: loading-img-spin infinite 1s linear;
  height: 200px;
}

@-webkit-keyframes loading-img-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes loading-img-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}



